import React from "react"
import styled from "styled-components"
import { Container, Row } from "reactstrap"
import { Element } from "react-scroll"

import { COLOR } from "../bases/constant"
import { Col } from "../bases/Layout"

const sessions = [
  {"time": "13:00 - 13:30", "session": ""},
  {"time": "13:30 - 14:00", "session": ""},
  {"time": "14:00 - 14:35", "session": "Session I"},
  {"time": "14:40 - 15:15", "session": "Session II"},
  {"time": "15:15 - 15:30", "session": " "},
  {"time": "15:30 - 16:05", "session": "Session III"},
  {"time": "16:10 - 16:30", "session": ""}
]

const TimetbleContainer = styled(Container)`
  background-color: ${COLOR.normal};
`
TimetbleContainer.defaultProps = {
  className: "py-5"
}

const Timetable = () => (
  <TimetbleContainer fluid>
    <Container >
      <Element className='row' name='timetable'>
        <Col className='text-center'>
          <h1>Timetable</h1>
        </Col>
        <Col>
          <span>
            {
              sessions.map(({session, time}) => <div key={`${time}${session}`}>{`${time} ${session}`}</div>)
            }
          </span>
        </Col>
      </Element>
    </Container>
  </TimetbleContainer>
)

export default Timetable



